@import "../../global.scss";

.contact {
    display: flex;

    @include mobile {
        flex-direction: column;
    }

    .left {
        flex: 1;
        overflow: hidden;

        img {
            height: 100%;
            width: 100%;
        }
    }

    .right {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h2 {
            font-size: 30px;
        }

        form {
            width: 70%;
            height: 70%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;

            input {
                width: 300px;
                height: 30px;
                font-size: 14px;
                border-radius: 5px;
                outline-width: 0;
                border: 1px solid purple;

                @include mobile {
                    width: 200px;
                    height: 20px;
                }
            }

            textarea {
                width: 300px;
                height: 200px;
                font-size: 14px;
                outline-width: 0;
                border: 1px solid purple;
                border-radius: 5px;

                @include mobile {
                    width: 200px;
                    height: 100px;
                }
            }

            button {
                width: 150px;
                height: 30px;
                color: white;
                background-color: crimson;
                border: none;
                border-radius: 10px;
                font-weight: 500;
                cursor: pointer;

                &:focus {
                    outline: none;
                }
            }

            span {
                color: white;
                background-color: purple;
                font-size: 1.4rem;
                padding: .3rem;
                border-radius: .4rem;
                font-weight: 600;
                transition: all 1s ease;
            }
        }
    }
}